.register{
    height: calc(100vh - 50px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* background: linear-gradient(rgba(255,255,255,0.5),rgba(255,255,255,0.5)), url("https://www.stockvault.net/data/2011/02/06/117454/thumb16.jpg"); */
    background-size:cover;

}

.registerTitle{
    font-size: 50px;
}

.registerForm{
    margin-top: 20px;
    display:flex;
    flex-direction: column;
}

.registerForm >label{
    /* top and bottom 10px, right and left 0px. */
    margin: 10px 0;
    font-size: 30px;
}

.registerInput{
    padding: 10px;
    background-color: white;
    border: none;
    font-size: 20px;

}

.registerButton{
    margin-top: 20px;
    cursor:pointer;
    background-color: teal;
    border: none;
    color: white;
    border-radius: 10px;
    padding: 10px;
    font-size: 20px;
}

.registerLoginButton{
    position: absolute;
    top: 60px;
    right: 20px;
    background-color: lightcoral;
    cursor: pointer;
    border: none;
    padding: 10px;
    color: white;
    border-radius: 10px;
    font-size: 20px;
}
@media screen and (min-width: 1143px) {
    .registerLoginButton {
      display: none;
    }
  }